@import '~assets/styles/h3Imports';

@mixin tabletUp {
  @media #{$tablet-up} {
    .description {
      align-items: center;
      padding-bottom: 0;

      .title {
        max-width: rem(195);
        width: rem(195);
      }
    }

    .info {
      align-items: center;
      border: none;
      display: inline-flex;
      flex-direction: row;
      margin: 0;
      padding: 0;

      :first-child {
        margin-right: rem(24);
      }

      .info-line {
        align-items: center;
        display: inline;
        flex-direction: column;
        justify-content: unset;
        width: rem(116);

        span {
          display: none;
        }

        &:first-child {
          justify-content: unset;
          margin-bottom: 0;
        }
      }
    }
  }
}

@mixin desktopUp {
  @media #{$post-large-up} {
    .description {
      .title {
        max-width: rem(310);
        width: rem(310);
      }
    }
  }
}

.description {
  display: inline-flex;
  flex-direction: row;
  padding-bottom: rem(16);

  .icon {
    align-items: center;
    display: inline-flex;
    margin-right: rem(16);

    span {
      align-items: center;
      display: inline-flex;
    }

    svg {
      height: rem(48);
      width: rem(48);
    }
  }

  [data-fontsize='AAA'] & {
    @media (max-width: 767px) {
      flex-direction: column;
      width: 100%;

      .icon {
        margin-left: auto;
        margin-right: auto;
      }
    }
  }

  .title {
    align-items: center;
    display: inline-flex;

    :first-child {
      color: var(--gray-5);
      font-size: rem(17);
      font-weight: 700;
      margin: 0;
    }

    &.non-active {
      :first-child {
        color: var(--gray-3);
      }
    }
  }
}

.info {
  border-bottom: 1px solid rgba(#323264, 0.1);
  border-top: 1px solid rgba(#323264, 0.1);
  margin-bottom: rem(16);
  padding: rem(16) 0;

  .info-line {
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    .title {
      color: var(--gray-3);
      font-size: rem(14);
      font-weight: 600;
    }

    .response {
      color: var(--gray-5);
      font-size: rem(16);
      font-weight: 400;
    }

    &:first-child {
      margin-bottom: rem(16);
    }
  }
}

.view-details {
  :first-child {
    font-size: rem(16);
    font-weight: 600;
    padding: 0;
    text-decoration: none;
    white-space: nowrap;

    &:focus {
      outline: var(--button-outline-color, #5995b3)
        var(--button-outline-style, solid) var(--button-outline-width, #{em(2)});
      outline-offset: var(--button-outline-offset, #{em(2)});
    }
  }
}

@include tabletUp;
@include desktopUp;
