@import '~assets/styles/h3Imports';

@mixin tabletUp {
  @media #{$tablet-up} {
    max-width: 100%;

    .information {
      align-items: center;
      display: inline-flex;
      flex-direction: row;
      justify-content: space-between;
      padding-bottom: rem(12);

      [data-fontsize='AA'] &,
      [data-fontsize='AAA'] & {
        align-items: initial;

        // align-items: flex-start;
        // flex-direction: column;
        flex-wrap: wrap;
        padding-right: 0;

        [data-element='fills'] {
          flex: 0 1 70%;
          max-width: 70%;
        }

        [data-element='link'] {
          display: flex;
          flex: 0 1 20%;
        }

        > *:first-child {
          margin-bottom: 0.75rem;
        }

        [data-element='title'] {
          max-width: 100%;
          width: 100%;

          > * {
            line-height: 1.25;
          }
        }
      }

      &.active-inactive-state {
        padding-bottom: 0;
      }
    }

    .segmented-question {
      align-items: center;
      display: inline-flex;
      flex-direction: row;
      margin-top: 0;
      padding-top: rem(12);

      .question {
        justify-content: left;
        margin-bottom: 0;
      }

      .segmented > :first-child {
        max-width: rem(195);
        width: rem(195);
      }
    }
  }
}

@mixin desktopUp {
  @media #{$post-large-up} {
    flex-direction: row;

    &[data-element='shows-information-true'] {
      [data-fontsize='AA'] &,
      [data-fontsize='AAA'] & {
        flex-wrap: wrap;

        [data-element='medinfo'] {
          flex-wrap: nowrap;
        }

        [data-element='MedicationIcon'] {
          flex: 0 1 3rem;
        }

        [data-element='title'] {
          flex: 0 1 auto;
        }

        [data-element='link'] {
          flex: 0 1 20%;
          margin-bottom: 0.75rem;
        }

        [data-element='segmented'] {
          border-left: 0;
          border-top: 1px solid rgba(50, 50, 100, 0.1);
          display: flex;
          flex: 0 1 100%;
          justify-content: space-between;
          padding-left: 0;
          padding-top: 0.75rem;
          width: 100%;
        }
      }
    }

    .information {
      padding-bottom: 0;
      padding-right: rem(24);
      width: 100%;
    }

    .segmented-question {
      border-left: 1px solid rgba(#323264, 0.1);
      border-top: none;
      justify-content: end;
      padding-left: rem(24);
      padding-top: 0;
      width: auto;

      .question {
        display: inline;
        margin-right: rem(16);
        white-space: nowrap;
        width: auto;
      }
    }
  }
}

.wrapper {
  background-color: var(--white);
  border-radius: 8px;
  box-shadow: var(--box-shadow);
}

.questions-container {
  border-radius: inherit;
  margin-top: -16px;
  padding: 0 16px 16px;
}

.card {
  background-color: var(--white);
  border-bottom: 0;
  border-radius: inherit;
  display: inline-flex;
  flex-direction: column;
  padding: rem(16);
  width: 100%;

  .information {
    width: auto;
  }

  .segmented-question {
    border-top: 1px solid rgba(#323264, 0.1);
    margin-top: rem(16);
    padding-top: rem(24);
    width: 100%;

    .question {
      display: inline-flex;
      justify-content: center;
      margin-bottom: rem(16);
      width: 100%;
    }

    .segmented {
      & * {
        border-width: 2px;
      }

      :first-child {
        height: rem(40);
        max-width: 100%;

        label {
          height: 100%;
        }

        :first-child,
        :last-child {
          align-items: center;
          display: inline-flex;
          font-size: rem(14);
          font-weight: 700;
          justify-content: center;
          padding: 0;
        }
      }
    }
  }

  @include tabletUp;
  @include desktopUp;
}
